import axios from "axios";
import Vue from "vue"
var that = new Vue()
import { Message, MessageBox } from "element-ui";
import store from "@/store";
import { getToken } from "@/utils/auth";
import qs from "qs";
import { showLoading, hideLoading } from "@/components/loading/loading";

const service = axios.create({
    // baseURL: process.env.VUE_APP_BASE_API, // url = base url + request url
    // withCredentials: true, // Cookie
    timeout: 60000 // 
});

// request interceptor
service.interceptors.request.use(
    config => {
        // 
        const { notShowLoading } = config.data || false;
        config.data = qs.stringify(config.data);
        config.headers["Content-Type"] = "application/x-www-form-urlencoded";
        // config.headers["Content-Type"] = "application/json";
        if (store.getters.token) {
            // Date.parse(new Date()) 
            // new Date().getTime() 
            // process.env.VUE_APP_FLAG  
            // (Math.random() * 1e16)  
            config.headers["Authentication"] = getToken();
            config.headers["time"] = new Date().getTime();
            config.headers["random"] = Math.random() * 1e16;
            const sign = new Date().getTime() + Math.random() * 1e16 + process.env.VUE_APP_FLAG
            config.headers["sign"] = that.$md5(sign).toUpperCase();
        }
        if (!notShowLoading) {
            showLoading();
        }
        return config;
    },
    error => { // 
        console.log(error); // 
        return Promise.reject(error);
    }
);

// 
service.interceptors.response.use(
    response => {
        hideLoading();
        const res = response.data;
        if (res.code !== 200) {
            if (res.code === 405) {
                const name = store.getters.name;
                const stylediv = "width: 90%;margin-bottom: 35px;";
                const styleInput =
                    "-webkit-appearance: none;height: 36px;line-height: 36px;background-color: #fff;background-image: none;border-radius: 4px;border: 1px solid #dcdfe6;box-sizing: border-box;color: #606266;display: inline-block;font-size: inherit;outline: none;padding: 0 15px;transition: border-color .2s cubic-bezier(.645,.045,.355,1);width: calc( 100% - 65px) ;";
                if (name) {
                    MessageBox.confirm(
                            `<div style="${stylediv}"><div style="${styleInput};text-align:left"  >${name}</div></div>` +
                            `<div style="${stylediv}"><input placeholder="" type = "password" id="possword" prefix-icon="el-icon-search"  style="${styleInput}"></input></div>`,
                            "", {
                                confirmButtonText: "",
                                cancelButtonText: "",
                                customClass: "confirm50Class whlogin",
                                dangerouslyUseHTMLString: true,
                                center: true,
                                showClose: false,
                                closeOnClickModal: false
                            }
                        )
                        .then(({ value }) => {
                            var obj = document.getElementById("possword");
                            store
                                .dispatch("user/login", { username: name, password: obj.value })
                                .then(() => {
                                    obj.value = "";
                                });
                        })
                        .catch(() => {
                            location.reload();
                        });
                }
            } else {
                const msg =
                    "" +
                    "</br></br>" +
                    "" +
                    res.code +
                    "</br></br>" +
                    "" +
                    res.msg;
                Message({
                    dangerouslyUseHTMLString: true,
                    message: msg,
                    type: "error",
                    duration: 5 * 1000,
                    showClose: true
                });
            }

            return Promise.reject(new Error(res.msg || "Error"));
        } else {
            return res;
        }
    },
    error => {
        hideLoading();
        console.log("err" + error); // for debug
        if (error.response.status === 403) {
            const msg =
                "" +
                "</br></br>" +
                "" +
                error.response.status +
                "</br></br>" +
                "" +
                error.message;
            Message({
                dangerouslyUseHTMLString: true,
                message: msg,
                type: "error",
                duration: 5 * 1000,
                showClose: true
            });
        } else {
            Message({
                message: error.message,
                type: "error",
                duration: 5 * 1000
            });
        }
        return Promise.reject(error);
    }
);

export default service;
