/**
 * 
 * 1.main.js
 * import $validate from './assets/js/validate'
 * Vue.prototype.$validate = $validate
 * 2. this.$validate.()
 */
 export default {
  filter_rules(item) {
    /** @description element form
     * @property {funtion} filter_rules   
     * @property {Boolean} required = [true|false]    
     * @property {String} type = [blank()|money()|mobile()|email()|num()]
     * @property {String} tips   
     * @property {Boolean} select = [true|false]    
     * type :rules="$validate.filter_rules({required:true,type:'blank',value:patrolTask.patrolId,tips:''})"
     * select :rules="$validate.filter_rules({select:true,tips:''})"
     */
    const rules = [];
    if (item.required) {
      rules.push({
        required: true,
        message: "" + item.tips,
        trigger: ['blur',"change"]
      });
    }
    if (item.select) {
      rules.push({ required: true, message: "" + item.tips });
    }
    if (item.maxLength) {
      rules.push({
        min: 1,
        max: item.maxLength,
        message: "" + item.maxLength + "!",
        trigger: "blur"
      });
    }
    if (item.min && item.max) {
      rules.push({
        min: item.min,
        max: item.max,
        message:
          item.tips + "" + item.min + "" + item.max + "!",
        trigger: "blur"
      });
    }
    if (item.type) {
      const type = item.type;
      switch (type) {
        case "num":
          rules.push({
            trigger: "blur",
            validator: function(rule, value, callback) {
              if (!/(^[0-9]\d*$)/.test(item.value)) {
                callback(new Error("" + item.tips));
              } else {
                callback();
              }
            }
          });
          break;
        case "email":
          rules.push({
            trigger: "blur",
            validator: function(rule, value, callback) {
              if (
                !/^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/.test(
                  item.value
                )
              ) {
                callback(new Error(""));
              } else {
                callback();
              }
            }
          });
          break;
        case "mobile": // 
          rules.push({
            trigger: "blur",
            validator: function(rule, value, callback) {
              if (!/^1[3456789]\d{9}$/.test(item.value)) {
                callback(new Error(""));
              } else {
                callback();
              }
            }
          });
          break;
        case "UDI": // 
          rules.push({
            trigger: "blur",
            validator: function(rule, value, callback) {
              if (!/^[0-9()]{30}$/.test(item.value)) {
                callback(
                  new Error("UDI30")
                );
              } else {
                callback();
              }
            }
          });
          break;
        case "id": // 
          rules.push({
            validator: function(rule, value, callback) {
              if (
                !/^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/.test(
                  item.value
                )
              ) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "blur,change"
          });
          break;
        case "chinese": // 
          rules.push({
            trigger: ["blur", "change"],
            validator: function(rule, value, callback) {
              if (/[^\u4E00-\u9FA5]/g.test(item.value)) {
                callback(new Error("" + item.tips));
              } else {
                callback();
              }
            }
          });
          break;
        case "noChinese": // 
          rules.push({
            trigger: ["blur", "change"],
            validator: function(rule, value, callback) {
              if (/[\u4E00-\u9FA5]/g.test(item.value)) {
                callback(new Error(item.tips + ""));
              } else {
                callback();
              }
            }
          });
          break;
        case "money": // 
          rules.push({
            trigger: ["blur", "change"],
            validator: function(rule, value, callback) {
              if (
                !/(^(([1-9]([0-9]+)?)|(0{1}))(\.[0-9]{1,2})?$)/.test(item.value)
              ) {
                callback(new Error("" + item.tips));
              } else {
                callback();
              }
            }
          });
          break;
        case "blank": // 
          rules.push({
            trigger: "blur",
            validator: function(rule, value, callback) {
              if (item.value !== undefined && item.value.trim() === "") {
                callback(new Error(""));
              } else {
                callback();
              }
            }
          });
          break;
        default:
          rules.push({});
          break;
      }
    }
    return rules;
  },
  pickerOptions(type, date) {
    if (type === "aftertoday") {
      // 
      return { disabledDate(time) { return time.getTime() < Date.now() - 8.64e7 } }
    } else if (type === "beforetoday") {
      // 
      return { disabledDate(time) { return time.getTime() > Date.now() - 8.64e6 } }
    } else if (type === "beforetoday") {
      // 
      return { disabledDate(time) { return time.getTime() < Date.now() } }
    } else if (type === "beforetoday") {
      // 
      return { disabledDate(time) { return time.getTime() > Date.now() } }
    } else if (type === "60") {
      // 60
      return {
        disabledDate(time) {
          const curDate = (new Date()).getTime();
          const three = 90 * 24 * 3600 * 1000;
          const threeMonths = curDate - three;
          return time.getTime() > Date.now() || time.getTime() < threeMonths;
        }
      }
    } else if (type === "6") {
      // 60
      return {
        disabledDate(time) {
          const curDate = (new Date()).getTime();
          const three = 90 * 24 * 3600 * 1000;
          const threeMonths = curDate - three;
          //  return time.getTime() < this.value1 || time.getTime() > Date.now();
          return time.getTime() > Date.now() || time.getTime() < threeMonths;
        }
      }
    } else if (type === "startData") {
      return {
        disabledDate: time => {
          if (date) { //    
            return (
              time.getTime() > new Date(date).getTime()
            )
          }
        }
      }
    } else if (type === "endDate") {
      return {
        disabledDate: time => { //  
          if (date) {
            return time.getTime() < new Date(date).getTime();
          }
        }
      }
    }
  },
  timeverify(type, data) {
    /** @description 
     * @property {funtion} timeverify(type,data)   
     *  type=0 
     *  type=1 
     *  type=3 data!="" d
     *  type=0 
     */
    var validityPickerOptions = {};
    if (type === 0) {
      validityPickerOptions = {
        disabledDate: time => {
          return time.getTime() < Date.now() - 8.64e7;
        }
      };
    } else if (type === 1) {
      validityPickerOptions = {
        disabledDate: time => {
          return time.getTime() > Date.now();
        }
      };
    }
    return validityPickerOptions;
  },
  formatDate(fmt) {
    /** @description 
     * @property {funtion} formatDate(fmt)   
     *  fmt="yyyy-MM-dd hh:mm:ss"
     */
    var myDate = new Date();
    var o = {
      "M+": myDate.getMonth() + 1, // 
      "d+": myDate.getDate(), // 
      "h+": myDate.getHours(), // 
      "m+": myDate.getMinutes(), // 
      "s+": myDate.getSeconds(), // 
      "q+": Math.floor((myDate.getMonth() + 3) / 3), // 
      S: myDate.getMilliseconds() // 
    };
    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(
        RegExp.$1,
        (myDate.getFullYear() + "").substr(4 - RegExp.$1.length)
      );
    }
    for (var k in o) {
      if (new RegExp("(" + k + ")").test(fmt))
        fmt = fmt.replace(
          RegExp.$1,
          RegExp.$1.length === 1
            ? o[k]
            : ("00" + o[k]).substr(("" + o[k]).length)
        );
    }
    return fmt;
  }
};


/**
 * @param {string} path
 * @returns {Boolean}
 */
export function isExternal(path) {
  return /^(https?:|mailto:|tel:)/.test(path)
}

/**
 * @param {string} str
 * @returns {Boolean}
 */
export function validUsername(str) {
  const valid_map = ['admin', 'editor']
  return valid_map.indexOf(str.trim()) >= 0
}
