import Cookies from "js-cookie";
import { login, logout, getUserInfo } from "@/api/user";
import { getToken, setToken, removeToken } from "@/utils/auth";
import { resetRouter } from "@/router";

const state = {
  sidebar: {
    opened: Cookies.get('sidebarStatus') ? !!+Cookies.get('sidebarStatus') : true,
    withoutAnimation: false,
  },
  logourl: "https://wpimg.wallstcn.com/69a1c46c-eb1c-4b46-8bd4-e9e686ef5251.png", // logo
  sidebartitle: "Vue Element Admin", // logo
  sidebarLogo: true, // logo
  device: 'desktop', // 
  size: Cookies.get('size') || 'medium', // 
  token: getToken(),
  name: "",
  avatar: "",
  introduction: "",
  roles: [],
  fixedHeader: true, // tag
  tagsView: true, // tag
};

const mutations = {
  // 
  SET_TOKEN: (state, token) => {
    state.token = token;
  },
  SET_NAME: (state, name) => {
    // 
    state.name = name;
  },
  SET_USERID: (state, userId) => {
    // id
    state.userId = userId;
  },
  SET_MENUS: (state, menus) => {
    // 
    state.menus = menus;
  },
  // end
  // logo
  TOGGLE_SIDEBAR: (state) => {
    state.sidebar.opened = !state.sidebar.opened;
    state.sidebar.withoutAnimation = false;
    if (state.sidebar.opened) {
      Cookies.set("sidebarStatus", 1);
    } else {
      Cookies.set("sidebarStatus", 0);
    }
  },
  CLOSE_SIDEBAR: (state, withoutAnimation) => {
    Cookies.set("sidebarStatus", 0);
    state.sidebar.opened = false;
    console.log(withoutAnimation)
    state.sidebar.withoutAnimation = withoutAnimation;
  },
  // logo end
  SET_SIZE: (state, size) => { // 
    state.size = size
    Cookies.set('size', size)
  },
  TOGGLE_DEVICE: (state, device) => { // 
    state.device = device
  },
  SET_INTRODUCTION: (state, introduction) => {
    state.introduction = introduction;
  },
  // SET_NAME: (state, name) => {
  //   state.name = name
  // },
  SET_AVATAR: (state, avatar) => {
    state.avatar = avatar;
  },
  SET_ROLES: (state, roles) => {
    state.roles = roles;
  },
};

const actions = {
  // user login
  login({ commit }, userInfo) { // 
    const { username, password } = userInfo;
    return new Promise((resolve, reject) => {
      login({ username: username.trim(), password: password })
        .then((response) => {
          const { data } = response;
          commit("SET_TOKEN", data.taken);
          setToken(data.taken);
          resolve();
        })
        .catch((error) => {
          reject(error);
        });
    });
  },

  getUserInfo({ commit, state }) { // 
    return new Promise((resolve, reject) => {
      getUserInfo()
        .then((response) => {
          const { data } = response;
          commit("SET_NAME", data.username);
          commit("SET_USERID", data.userid);
          commit("SET_MENUS", data.menus);
          resolve(data);
        })
        .catch((error) => {
          reject(error);
        });
    });
  },

  // user logout
  logout({ commit, state, dispatch }) { // 
    return new Promise((resolve, reject) => {
      logout(state.token)
        .then(() => {
          commit("SET_TOKEN", "");
          commit("SET_ROLES", []);
          removeToken();
          resetRouter();
          // reset visited views and cached views
          // to fixed https://github.com/PanJiaChen/vue-element-admin/issues/2485
          dispatch("tagsView/delAllViews", null, { root: true });
          resolve();
        })
        .catch((error) => {
          reject(error);
        });
    });
  },

  // remove token
  resetToken({ commit }) {
    return new Promise((resolve) => {
      commit("SET_TOKEN", "");
      commit("SET_ROLES", []);
      removeToken();
      resolve();
    });
  },

  toggleSideBar({ commit }) { // SideBar
    commit('TOGGLE_SIDEBAR')
  },
  closeSideBar({ commit }, { withoutAnimation }) { // SideBar
    commit('CLOSE_SIDEBAR', withoutAnimation)
  },
  setSize({ commit }, size) { // 
    commit('SET_SIZE', size)
  },
  toggleDevice({ commit }, device) { // 
    commit('TOGGLE_DEVICE', device)
  },
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
};
