import Vue from 'vue'
import Cookies from 'js-cookie'
import 'normalize.css/normalize.css' // a modern alternative to CSS resets
import Element from 'element-ui'

import './assets/css/styles/element-variables.scss'
import '@/assets/css/styles/index.scss' // global css
import App from './App'
import store from './store'
import router from './router'
import "./components/icons"; // icon
import './permission' // permission control
import '@/assets/css/index.scss'
import "@/assets/css/common.scss";
import directive from "./utils/directives";// 
Vue.use(directive);
import md5 from 'js-md5';// md5
Vue.prototype.$md5 = md5;
import $validate from "./assets/js/validate";// form
Vue.prototype.$validate = $validate;
import VueDND from 'awe-dnd'// 
Vue.use(VueDND)
var src = window.location.href; // 
const tempIndex = src.indexOf("/", 7);
const urls = src.substring(0, tempIndex);
Vue.prototype.$fileUrl =
  urls.indexOf("localhost") === -1 ? urls : process.env.VUE_APP_BASE_URL;

import * as filters from './filters' // global filters

Vue.use(Element, {
  size: Cookies.get('size') || 'medium', // set element-ui default size
})

// register global utility filters
Object.keys(filters).forEach(key => {
  Vue.filter(key, filters[key])
})

Vue.config.productionTip = false

new Vue({
  el: '#app',
  router,
  store,
  // i18n,
  render: h => h(App)
})
