// 
const directives = {
  debounce: { // 
    //  <el-input v-model="aa" v-debounce="[reset,`input`,1000]" placeholder="" />
    // <el-button v-debounce="[reset,`click`,1000]"></el-button>
    inserted: function(el, binding) {
      const [fn, event = "click", time = 1000] = binding.value;
      let timer;
      let flag = true; // 
      if (event === "input") {
        el.addEventListener("compositionstart", () => { // 
          flag = false;
        });
        el.addEventListener("compositionend", () => { // 
          flag = true;
        });
      }
      el.addEventListener(event, () => {
        timer && clearTimeout(timer);
        timer = setTimeout(() => {
          if (flag) fn();
        }, time);
      });
    },
  },
  inputDebounce: { // 
    inserted: function(el, binding) {
      const fn = binding.value;
      const time = binding.arg || 500;
      let timer;
      let flag = true; // 
      el.addEventListener("compositionstart", () => { // 
        flag = false;
      });
      el.addEventListener("compositionend", () => { // 
        flag = true;
      });
      el.addEventListener('input', () => {
        timer && clearTimeout(timer);
        timer = setTimeout(() => {
          if (flag) fn();
        }, time);
      });
    },
  },
  btnDebounce: { // 
    inserted: (el, binding) => {
      const time = binding.arg || 1000; // 
      let debounce = null;
      el.addEventListener('click', event => {
        if (debounce !== null) {
          clearTimeout(debounce)
          event && event.stopImmediatePropagation();
        }
        debounce = setTimeout(() => {
          debounce = null
        }, time)
      }, true);
    }
  },
  btnThrottle: { // 
    inserted: (el, binding) => {
      const time = binding.arg || 1000; // 
      let cbFun;
      el.addEventListener('click', event => {
        if (!cbFun) { // 
          cbFun = setTimeout(() => {
            cbFun = null;
          }, time);
        } else {
          event && event.stopImmediatePropagation();
        }
      }, true);
    }
  },
  relativeTime: { // 
    bind(el, binding) {
      var intervalTime = 60000
      var Time = {
        getUnix: function() { // 
          var date = new Date();
          return date.getTime();
        },
        getTodayUnix: function() { // 000
          var date = new Date();
          date.setHours(0);
          date.setMinutes(0);
          date.setSeconds(0);
          date.setMilliseconds(0);
          return date.getTime();
        },
        getYearUnix: function() { // 11000
          var date = new Date();
          date.setMonth(0);
          date.setDate(1);
          date.setHours(0);
          date.setMinutes(0);
          date.setSeconds(0);
          date.setMilliseconds(0);
          return date.getTime();
        },
        getLastDate: function() { // 
          var date = new Date();
          var month = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1;
          var day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
          return date.getFullYear() + '-' + month + '-' + day;
        },
        getFormatTime: function(timestamp) { // 
          var now = this.getUnix();// 
          var today = this.getTodayUnix();// 0
          var timer = (now - timestamp) / 1000;// 
          var tip = '';
          if (timer <= 0) {
            tip = '';
          } else if (Math.floor(timer / 60) <= 0) {
            tip = '';
          } else if (timer < 3600) {
            tip = Math.floor(timer / 60) + '';
          } else if (timer >= 3600 && (timestamp - today >= 0)) {
            tip = Math.floor(timer / 3600) + '';
            intervalTime = 3600000;
          } else if (timer / 86400 <= 31) {
            tip = Math.ceil(timer / 86400) + '';
            intervalTime = 3600000 * 24;
          } else {
            tip = this.getLastDate(timestamp);
            intervalTime = 3600000 * 24;
          }
          return tip;
        }
      }
      el.innerHTML = Time.getFormatTime(binding.value)
      el.__timeout__ = setInterval(() => {
        el.innerHTML = Time.getFormatTime(binding.value)
      }, intervalTime)
    },
    unbind(el) {
      clearInterval(el.innerHTML)
      delete el.__timeout__
    }
  },
  role: {
    inserted: function(el, binding, vnode) {
      const role = [{ label: "", icon: "", type: 'add', btnName: 'nocoop_ent_qry' },
      // { label: "", icon: "icon-edit", type: 'edit', btnName: 'coop_ent_oper' },
      { label: "", icon: "icon-eye", type: 'detail', btnName: 'coop_ent_detail' }];
      // console.log(vnode.context.$route.meta)
      if (!role.some(item => item.type === vnode.key) && el.parentNode.children.length <= 1) {
        el.parentNode.parentNode.remove()
      } else if (!role.some(item => item.type === vnode.key)) {
        el.remove()
      }
    }
  }
}
// 
export default {
  install(Vue) {
      Object.keys(directives).forEach((key) => {
          Vue.directive(key, directives[key]);
      });
  },
};

