import router from './router'
import store from './store'
// import { Message } from "element-ui";
import NProgress from 'nprogress' // 
import 'nprogress/nprogress.css' // 
import { getToken } from '@/utils/auth' // get token from cookie
import Layout from '@/views/layout'
import BaseRouter from './views/layout/baseRouter'
// import getPageTitle from "@/utils/get-page-title";

NProgress.configure({ showSpinner: false }) // 
const whiteList = ['/login', '/auth-redirect'] // 

router.beforeEach(async(to, from, next) => {
  NProgress.start() // 
  if (getToken()) {
    if (to.path === '/login') {
      next({ path: '/index' })
      NProgress.done() // hack: https://github.com/PanJiaChen/pull/2939
    } else {
      if (store.getters.name) { // getuserInfo vuexid/
        next()
      } else { // getuserInfo
        // eslint-disable-next-line require-atomic-updates
        try {
          await store.dispatch('user/getUserInfo')
          const menus = []
          filterAsyncRouter(menus, store.getters.menus, 0) // 1.
          router.addRoutes(menus) // 2.
          global.antRouter = menus // 3.
          next({ ...to, replace: true })
        } catch (error) {
          // eslint-disable-next-line require-atomic-updates
          await store.dispatch('user/resetToken')
          // Message.error(",.");
          next(`/login?redirect=${to.path}`)
          NProgress.done()
        }
      }
    }
  } else {
    /* has no token*/
    if (whiteList.indexOf(to.path) !== -1) {
      next()
    } else {
      next(`/login?redirect=${to.path}`)
      NProgress.done()
    }
  }
})

router.afterEach(() => {
  // const menus = [];
  // filterAsyncRouter(menus, store.getters.menus, 0); // 1.
  // finish progress bar
  NProgress.done()
})
function filterAsyncRouter(routes, asyncRouterMap, i) {
  if (asyncRouterMap) {
    asyncRouterMap.forEach(item => {
      const menu = {
        path: item.route,
        name: item.routeName,
        meta: {
          title: item.menuName,
          icon: item.menuIcon
        },
        hidden: item.routeType === 1, // menuType    0. 1.
        children: []
      }
      if (i !== 0) {
        if (item.children && item.children.length > 0) {
          menu.component = BaseRouter
        } else {
          // // 
          if (item.buttonList) {
            //   btnPlace 1  2
            const operationbtn = item.buttonList.filter((item) => item.btnPlace === 1)
            const oldtablebtn = item.buttonList.filter((item) => item.btnPlace !== 1)

            const Obj = oldtablebtn.reduce((pre, cur, index) => {
              if (!pre[cur.label]) { // 
              // 
                pre[cur['label']] = { pram: cur['name'], label: cur['label'], btn: true, btnType: cur['btnFlag'] }
              } else {
                if (pre[cur.label]['pram'] instanceof Array) { // 
                  pre[cur.label]['pram'].push({ btnlable: cur['name'], btnType: cur['btnFlag'], isIcon: cur['btnType'] })
                } else {
                  pre[cur.label] = { pram: [{ btnlable: pre[cur.label]['pram'], btnType: pre[cur.label]['btnType'], isIcon: pre[cur.label]['btnType'] }], label: cur['label'], btn: true }
                  pre[cur.label]['pram'].push({ btnlable: cur['name'], btnType: cur['btnFlag'], isIcon: cur['btnType'] })
                }
              }
              return pre
            }, {})
            const newtablebtn = Object.keys(Obj).map((item) => {
              return Obj[item]
            })
            menu.meta = { ...menu.meta, tablebtn: newtablebtn, operationbtn: operationbtn }
          }
          menu.component = resolve =>
            require([`@/views${item.route}`], resolve) // import('./views' + item.route)
        }
        menu.name = item.routeName
        menu.meta.activeMenu = item.route.substr(
          0,
          item.route.lastIndexOf('/')
        )
      } else {
        if (item.children && item.children.length > 0) {
          menu.component = Layout
          menu.name = item.routeName
        } else {
          menu.path = item.route.substr(0, item.route.indexOf('/'))
          menu.component = Layout
          menu.name = item.routeName
          menu.children = [
            {
              path: item.route.substr(item.route.indexOf('/') + 1),
              name: item.routeName,
              component: resolve => require([`@/views${item.route}`], resolve),
              xxxis: './views',
              meta: {
                title: item.menuName,
                icon: item.menuIcon,
                noCache: item.menuType === 1,
                activeMenu: item.route.substr(0, item.route.lastIndexOf('/'))
              }
            }
          ]
        }
      }
      if (item.children && item.children.length > 0) {
        filterAsyncRouter(menu.children, item.children, 1)
      }
      routes.push(menu)
    })
  }
}
