import { Loading } from "element-ui";
let loadingCount = 0;
let loading;
export const showLoading = () => {
  if (loadingCount === 0) {
    loading = Loading.service({
      lock: true,
      text: "",
      background: "rgba(0, 0, 0, 0.7)",
      target: document.querySelector(".app-main") // 
    });
  }
  loadingCount += 1;
};

export const hideLoading = () => {
  if (loadingCount <= 0) {
    return;
  }
  loadingCount -= 1;
  if (loadingCount === 0) {
    loading.close();
  }
};
